@import "../../../../assets/styles/variables";

.ServiceDetails {
    width: 100%;
    height: 100vh;

    .service-details-wrap {
        width: 100%;
        height: 100%;
        padding-inline: 100px;
        padding-bottom: 100px;
        padding-top: 30px;
        display: flex;
        justify-content: center;
        overflow-y: auto;

        @include md {
            padding-inline: 5px;
        }
    }

    ::-webkit-scrollbar {
        display: none;
    }

    .service-container {
        width: 70%;
        height: fit-content;
        display: flex;
        flex-direction: column;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
        padding: 15px;
        padding-inline: 25px;
        background-color: white;
        border-radius: 5px;

        @include lg {
            width: 100%;
        }

        .seller {
            display: flex;
            width: 100%;
            height: 45px;
            align-items: center;
            margin-bottom: 10px;
            padding-bottom: 5px;
            border-bottom: 1px solid $border-color-3;

            .profile-img {
                height: 100%;
                aspect-ratio: 1/1;
                display: flex;
                justify-content: center;

                img {
                    width: 100%;
                    height: 100%;
                    aspect-ratio: 1/1;
                    border-radius: 50%;
                }

            }

            .container1 {
                display: flex;
                height: 100%;

                .name {
                    margin-left: 10px;
                    font-size: 18px;
                    text-align: center;
                    display: flex;
                    align-items: center;
                }

                .row {
                    display: flex;
                    height: 100%;
                    margin-left: 10px;
                    align-items: center;
                    justify-content: center;

                    .rating-num {
                        margin-right: 10px;
                        font-size: 18px;

                    }

                    .rating-sub {
                        display: flex;
                        justify-content: left;

                        .rating-star {}

                        .reviews {
                            margin-left: 10px;
                            padding-left: 10px;
                            border-left: 2px solid $primary-color;
                        }
                    }

                }

            }

            .fa-cart-shopping,
            .fa-share-nodes {
                margin-left: 15px;
                font-size: 22px;
                color: $primary-color;
                display: flex;
                align-items: center;

                &:hover {
                    font-size: 23px;
                }
            }

            .fa-share-nodes {
                margin-left: auto;
            }
        }

        .service-img {
            width: 100%;
            height: fit-content;
            margin-bottom: 5px;
            position: relative;

            img {
                width: 100%;
                aspect-ratio: 1/0.6;
                object-fit: cover;
            }

            &::before {
                top: 0;
                left: 0;
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: red;
            }


            // &:empty {
            //     background-color: $border-color-3;
            // }

            // &:hover::after {
            //     animation: skeletonAnim 5s;
            // }

            // @keyframes skeletonAnim {
            //     from {
            //         left: -100%;
            //     }

            //     to {
            //         left: 0;
            //     }
            // }
        }

        .title {
            font-size: 30px;
            margin-bottom: auto;
            font-weight: 400;
            margin-bottom: 15px;
        }

        .buttons {
            display: flex;
            width: 100%;
            margin-top: 20px;
            margin-bottom: 10px;
            justify-content: space-between;

            .riple-btn{
                width: 47%;
                height: 55px;
                margin-top: 15px;
            }

            button {
                width: 100%;
                height: 100%;
                border: 0;
                font-size: 18px;
                color: white;
                background-color: $primary-color;
                border-radius: 5px;
                cursor: pointer;
            }

            .view-profile-btn {}

            .contact-now-btn {
                background-color: transparent;
                color: $primary-color;
                border: 1px solid $primary-color;
            }
        }

        h1 {
            opacity: 0.7;
            margin-top: 15px;
        }

        .details {
            margin-top: 15px;
            font-size: 18px;
            text-align: justify;
            color: $text-color;
            line-height: 33px;
            padding-bottom: 10px;
        }

        .ReviewList {
            width: 100%;
            height: fit-content;
            margin-top: 50px;
        }
    }

    .link-share-modal {
        position: relative;
        width: 400px;
        height: 230px;
        display: flex;
        padding: 20px;
        padding-top: 0px;
        padding-bottom: 0px;
        flex-direction: column;

        .links {
            display: flex;
            width: 100%;
            height: 100px;
            justify-content: space-around;
        }

        .title {
            font-size: 18px;
        }

        .copy-row {
            width: 100%;
            height: 40px;
            display: flex;
            margin-top: 20px;
            align-items: center;
            border: 1px solid $border-color-2;
            padding-inline: 5px;
            border-radius: 10px;

            i {
                margin-left: 15px;
            }

            input {
                width: 70%;
                height: 100%;
                border: 0;
                margin-left: 10px;

                &:focus {
                    outline: 0;
                }
            }
            .riple-btn{
                width: 80px;
                height: 80%;
                border: 0;
            }
            button {
                width: 100%;
                height: 100%;
                border: 0;
                background-color: $primary-color;
                color: white;
                border-radius: 10px;
                margin-left: 10px;
            }
        }
    }

    .popup-screen {
        width: 100%;
        height: 100%;
        z-index: 100;
        padding-inline: 100px;
        position: absolute;
        top: 0;
        left: 0;
        display: grid;
        place-content: center;
        background-color: $border-color-3;

        .popup-container {
            width: 100%;
            min-width: 600px;
            display: flex;
            flex-direction: column;
            height: fit-content;
            background-color: white;
            border-radius: 5px;
            padding-inline: 10px;
            overflow: hidden;
            position: relative;
            box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px,
                rgba(17, 17, 26, 0.1) 0px 8px 24px,
                rgba(17, 17, 26, 0.1) 0px 16px 48px;

            .popup-close-btn {
                position: absolute;
                top: 5px;
                right: 5px;
                width: 30px;
                height: 30px;
                display: grid;
                place-content: center;

                i {
                    font-size: 22px;

                    &:hover {
                        color: red;
                    }
                }
            }
        }

        .CartItem {
            margin-bottom: 10px;
            padding-bottom: 20px;
            border-bottom: 1px solid $border-color-3;
        }

        .popup-content {
            margin-inline: 10px;
            margin-bottom: 15px;
            padding-right: 20px;
            width: 100%;
            display: flex;
            flex-direction: column;

            label {
                margin-bottom: 10px;
                font-weight: 500;
                font-size: 16px;
            }

            textarea {
                width: 100%;
                margin-bottom: 10px;
                border: 2px solid $border-color-2;
                border-radius: 5px;
                font-size: 16px;
                height: 150px;
                resize: none;
                font-size: 16px;
                margin-top: 10px;
                padding: 5px;
            }

            textarea:focus {
                border: none;
                outline: 2px solid $border-color;
            }

            .place-order-btn {
                width: 120px;
                height: 30px;
                margin-top: 15px;
                border: 0px;
                border-radius: 5px;
                background-color: $primary-color;
                color: white;
                margin-left: auto;
            }

            .row {
                display: flex;
                align-items: center;
            }

            .spacer {
                margin-left: auto;
            }

            .loader-container {
                display: grid;
                width: 40px;
                height: 40px;
                place-content: center;
                position: relative;

                i {
                    position: absolute;
                    top: 5px;
                    left: 5px;
                    font-size: 30px;
                    color: $primary-color;

                }
            }
        }
    }

    .seller-to-buyer {
        position: relative;
        width: 400px;
        height: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        .riple-btn {
            width: 80%;
            height: 50px;
            margin-bottom: 10px;
        }

        .title{
            font-size: 20;
            font-weight: 500;
            margin-bottom: 10px;
        }

        button {
            width: 100%;
            height: 50px;
            border-radius: 10px;
            font-size: 20px;
            border: 0;
            background-color: $primary-color;
            color: white;
            position: relative;
            overflow: hidden;
        }
    }
}